@mixin reset-list() {
  list-style: none;
  padding: 0;
}

@mixin screen-reader() {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

@mixin vstack($alignment: initial) {
  display: flex;
  flex-direction: column;
  align-items: $alignment;
  > * {
    margin-top: 0;
    margin-bottom: 0;
  }
}

@mixin hstack {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  > * {
    margin-top: 0;
    margin-bottom: 0;
  }
}

@mixin stretch($position: absolute) {
  position: $position;
  inset: 0;
  width: 100%;
  height: 100%;
}

// Hover only for mouse users, focus only for keyboard
@mixin hover($focus: null) {
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      @content;
    }
  }

  @if $focus == focus {
    &:focus-visible {
      @content;
    }
  }
}

// Invisible space for better link and button select
@mixin target-space($size: 44px, $width: 100%, $height: 100%) {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    display: block;
    width: $width;
    height: $height;
    min-width: $size;
    min-height: $size;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
  }
}

//@url: Base64-encoded Data URI. Example: url("data:image/svg+xml..."). Encode online - https://yoksel.github.io/url-encoder/
@mixin mask-icon($url, $size: contain, $position: center) {
  content: "";
  mask-image: $url;
  mask-size: $size;
  mask-position: $position;
  mask-repeat: no-repeat;
  width: var(--icon-size);
  height: var(--icon-size);
  background-color: var(--icon-color);
}

@mixin element-hidden() {
  display: grid;
  grid-template-rows: 0fr;
  visibility: hidden;
  opacity: 0;
  transition:
    grid-template-rows var(--transition),
    opacity var(--transition),
    visibility var(--transition);
}

@mixin element-visible() {
  grid-template-rows: 1fr;
  opacity: 1;
  visibility: visible;
}

@mixin ratio($width, $height) {
  padding-bottom: calc(100% / ($width / $height));
}

@mixin will-change($styles: translate) {
  @include bp(md) {
    will-change: $styles;
  }
}
